import React, { Component, Suspense } from 'react';
import { Accordion, OverlayTrigger, Tooltip } from 'react-bootstrap';
const ColorPicker = React.lazy(() => import('../../../../CommonComponents/ColorPicker'));
class OnSiteUploads extends Component {
    render() {
        const { onSiteUpload, onOnSiteBgColor, onOnSiteBorderRadius, onOnSiteFontSize, onOnSiteTextColor, onOnSiteButtonText, onOnSiteButtonStatus, onOnSiteQrCodeStatus } = this.props
        return <Accordion.Item eventKey="5" className="pb-3">
            <Accordion.Header className="w-100">
                <span className="fs-7 fw-semibold">On-Site Upload</span>
            </Accordion.Header>
            <Accordion.Body className="border rounded-1 mt-1 p-2">
                <label className="d-flex align-items-center justify-content-between border-bottom h-40px py-1">
                    <span className="ms-0 fs-8 text-gray-800 fw-semibold">Background Color</span>
                    <Suspense fallback={<></>}>
                        <ColorPicker color={onSiteUpload.bgColor} onChangeColor={onOnSiteBgColor} />
                    </Suspense>
                </label>
                <div className="d-flex align-items-stretch flex-column justify-content-between border-bottom py-1">
                    <div className="">
                        <p className="my-2"><span className="fs-8 text-gray-800 fw-semibold">Border Radius</span></p>
                    </div>
                    <div className="d-flex align-items-center w-100 mb-1">
                        <div className="d-flex flex-grow-1 pe-3">
                            <div className="form-group pt-1 w-100">
                                <input type="range" className="form-range" min="0" max="30" step="1" value={onSiteUpload.borderRadius} defaultValue={onSiteUpload.borderRadius} id="formControlRange" onChange={onOnSiteBorderRadius} />
                            </div>
                        </div>
                        <div className="d-flex align-items-center">
                            <div className="form-group me-1">
                                <input type="number" className="form-control form-control-sm w-54px" min="0" max="30" id="fontSize" value={onSiteUpload.borderRadius} defaultValue={onSiteUpload.borderRadius} onChange={onOnSiteBorderRadius} />
                            </div>
                            px
                        </div>
                    </div>
                </div>
                <div className="d-flex align-items-stretch flex-column justify-content-between border-bottom py-1">
                    <div className="">
                        <p className="my-2"><span className="fs-8 text-gray-800 fw-semibold">Font Family</span></p>
                    </div>
                    <div className="w-100 mb-2">
                        <select className="form-control form-control-sm w-100 fw-semibold">
                            <option defaultValue>Open sans</option>
                            <option value="1">Roboto Sans</option>
                            <option value="2">Nunito Sans</option>
                            <option value="3">Popins</option>
                        </select>
                    </div>
                    <div className="w-100 mb-2">
                        <div className="row gx-1">
                            <div className="col-5">
                                <p className="my-2"><span className="fs-8 text-gray-800 fw-semibold">Font Varient</span></p>
                            </div>
                            <div className="col-7">
                                <select className="form-control form-control-sm w-100 fw-semibold">
                                    <option defaultValue>Regular</option>
                                    <option value="1">Medium</option>
                                    <option value="2">Bold</option>
                                    <option value="3">Black</option>
                                </select>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="d-flex align-items-stretch flex-column justify-content-between border-bottom py-1">
                    <div className="">
                        <p className="my-2"><span className="fs-8 text-gray-800 fw-semibold">Font Size</span></p>
                    </div>
                    <div className="d-flex align-items-center w-100 mb-1">
                        <div className="d-flex flex-grow-1 pe-3">
                            <div className="form-group pt-1 w-100">
                                <input type="range" className="form-range" min="10" max="30" step="1" id="formControlRange" value={onSiteUpload.fontSize} defaultValue={onSiteUpload.fontSize} onChange={onOnSiteFontSize} />
                            </div>
                        </div>
                        <div className="d-flex align-items-center">
                            <div className="form-group me-1">
                                <input type="number" className="form-control form-control-sm w-54px" min="10" max="30" id="fontSize" value={onSiteUpload.fontSize} defaultValue={onSiteUpload.fontSize} onChange={onOnSiteFontSize} />
                            </div>
                            px
                        </div>
                    </div>
                </div>
                <label className="d-flex align-items-center justify-content-between border-bottom h-40px py-1">
                    <span className="ms-0 fs-8 text-gray-800">Text Color</span>
                    <Suspense fallback={<></>}>
                        <ColorPicker color={onSiteUpload.textColor} onChangeColor={onOnSiteTextColor} />
                    </Suspense>
                </label>
                <div className="d-flex align-items-stretch flex-column justify-content-between border-bottom py-1">
                    <div className="">
                        <p className="my-2"><span className="fs-8 text-gray-800 fw-semibold">Button Text</span></p>
                    </div>
                    <div className="w-100 mb-2">
                        <input type="text" className="form-control form-control-sm w-100 fw-semibold" value={onSiteUpload.buttonText} onChange={onOnSiteButtonText} />
                    </div>
                </div>
                <label className="form-check form-switch py-2 align-items-center justify-content-between border-bottom h-40px py-1">
                    <span className="form-check-label ps-0 fs-8 text-gray-800">Button</span>
                    <input className="form-check-input" type="checkbox" name="shareIcons" checked={onSiteUpload.buttonStatus} onClick={onOnSiteButtonStatus} />
                </label>
                <label className="form-check form-switch py-2 align-items-center justify-content-between h-40px py-1">
                    <span className="form-check-label ps-0 fs-8 text-gray-800">QR (Show/Hide)</span>
                    <input className="form-check-input" type="checkbox" name="shareIcons" checked={onSiteUpload.qrCodeStatus} onClick={onOnSiteQrCodeStatus} />
                </label>
            </Accordion.Body>
        </Accordion.Item>
    }
}
export default OnSiteUploads